@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    -webkit-tap-highlight-color: transparent;
    outline: none;
  }

  /* common */
  :root {
    --color-theme-switch-light: #ffdd33;
    --color-theme-switch-dark: #142c61; /* dark --brand-secondary */

    /* chart color palette */
    --color-chart-grey: #4d596f;
  }

  body.dark {
    /* from figma color color inventory */
    --background-global-canvas: #070f1d;
    --background-level-1: #021531;
    --background-level-1-raw: 2, 21, 49;
    --background-level-2: #152647;
    --background-level-3: #34486a;

    --text-primary: #ffffff;
    --text-primary-alt: rgba(255, 255, 255, 0.32);
    --text-secondary: #8797b0;
    --text-tertiary: #465b7c;
    --text-inverted: #031329;

    --border-level-1: #1d3053;
    --border-level-2: #34486a;
    --border-level-3: #5f7291;

    --brand-primary: #3689ff;
    --brand-primary-alt: rgba(27, 115, 238, 0.24);
    --brand-secondary: #142c61;
    --brand-secondary-alt: #304574;
    --brand-tertiary: #5ea1ff;
    --brand-positive: #33c66d;
    --brand-positive-alt: rgba(51, 198, 109, 0.16); /* #33c66d */
    --brand-negative: #fb6060;
    --brand-negative-alt: rgba(239, 58, 58, 0.16); /* #fb6060 */
    --brand-sell: #ef3a3a;

    --accent-purple: #a09cff;
    --accent-purple-alt: #7b75ff;
    --accent-teal: #2ac2db;
    --accent-teal-alt: #2197ab;
    --accent-yellow: #ffe959;
    --accent-yellow-alt: #f6c604;

    --validation-alert: #fb6060;
    --validation-alert-alt: #4c2139;
    --validation-success: #33c66d;
    --validation-success-alt: #0c4045;
    --validation-warning: #ff8a00;
    --validation-warning-alt: #523b26;
    --validation-info: #368aff;
    --validation-info-alt: #113b78;

    /* other */
    --color-divider: var(--border-level-1);
    --color-handle: var(--text-secondary);

    --color-notification-success: var(--brand-positive);
    --color-notification-warning: var(--validation-warning);
    --color-notification-error: var(--brand-negative);
    --color-notification-info: var(--brand-tertiary);
    --color-empty-avatar-outline: #fff;
    --color-empty-avatar-fill: var(--accent-purple-alt);
    --color-close-button-stroke: var(--background-level-1);
    --color-close-button-fill: var(--text-primary-alt);

    --text-color-chart-tooltip-primary: var(--text-inverted);
    --text-color-chart-tooltip-secondary: var(--text-secondary);
    --text-button-primary: var(--text-primary);
    --text-button-secondary: var(--text-primary);
    --text-link-primary: var(--brand-tertiary);

    --background-overlay: rgba(0, 0, 0, 0.3);
    --background-skeleton-main: rgba(0, 0, 0, 0.3);
    --background-skeleton-glow: rgba(0, 0, 0, 0.4);
    --background-dot-active: var(--text-secondary);
    --background-input-disabled: var(--background-level-1);

    --background-notification-success: var(--validation-success-alt);
    --background-notification-warning: var(--validation-warning-alt);
    --background-notification-error: var(--validation-alert-alt);
    --background-notification-info: var(--validation-info-alt);

    --border-color-button-active: var(--text-secondary);
    --border-color-button-outline: var(--border-level-2);
    --border-dot-outline: var(--background-dot-active);
    --border-tab-indicator: var(--text-primary);
    --border-account-selector: var(--border-level-1); /* diff with white theme */

    --color-asset-type-EQ: #cc7400;
    --color-asset-type-BO: #4054ba;
    --color-asset-type-ETH: #368aff;
    --color-asset-type-ETF: #239f7a;
    --color-asset-type-MF: #9434cb;
    --color-asset-type-WA: #a54fc1;
    --color-asset-type-CO: #cc5600;
    --color-asset-type-ETC: #239f7a;
    --color-asset-type-FX: rgb(0, 120, 15); /** from stgo **/
    --color-asset-type-FX: rgb(0, 120, 15); /** from stgo **/
    --color-asset-type-FU: rgb(80, 95, 130); /** from stgo **/
    --color-asset-type-CFD: rgb(162, 38, 52); /** from stgo **/
    --color-asset-type-INDEX: rgb(10, 95, 180); /** from stgo **/
  }

  body.white {
    /* from figma color inventory */
    --background-global-canvas: #f0f4fa;
    --background-level-1: #ffffff;
    --background-level-1-raw: 255, 255, 255;
    --background-level-2: #e8eefc;
    --background-level-3: #ced7ee;

    --text-primary: #031329;
    --text-primary-alt: rgba(3, 19, 41, 0.32); /* #031329 */
    --text-secondary: #5f7495;
    --text-tertiary: #96afde;
    --text-inverted: #ffffff;

    --border-level-1: #edf2fd;
    --border-level-2: #ced7ee;
    --border-level-3: #b8c7db;

    --brand-primary: #1b73ee;
    --brand-primary-alt: rgba(27, 115, 238, 0.24);
    --brand-secondary: #e0e8fa;
    --brand-secondary-alt: #c5cee1;
    --brand-tertiary: #105fcc;
    --brand-positive: #28b25f;
    --brand-positive-alt: rgba(40, 178, 95, 0.12); /* #28b25f */
    --brand-negative: #ef3a3a;
    --brand-negative-alt: rgba(239, 58, 58, 0.12); /* #ef3a3a */
    --brand-sell: #ef3a3a;

    --accent-purple: #7b75ff;
    --accent-purple-alt: #a09cff;
    --accent-teal: #2ac2db;
    --accent-teal-alt: #6bdff2;
    --accent-yellow: #ffd500;
    --accent-yellow-alt: #ffe959;

    --validation-alert: #ef3a3a;
    --validation-alert-alt: #fac0c0;
    --validation-success: #28b25f;
    --validation-success-alt: #bae6cc;
    --validation-warning: #ff8a00;
    --validation-warning-alt: #ffd9ad;
    --validation-info: #1b73ee;
    --validation-info-alt: #c8ddfb;

    /* other */
    --color-divider: var(--border-level-1);
    --color-handle: var(--text-secondary);

    --color-notification-success: var(--brand-positive);
    --color-notification-warning: var(--validation-warning);
    --color-notification-error: var(--brand-negative);
    --color-notification-info: var(--brand-primary);
    --color-empty-avatar-outline: #fff;
    --color-empty-avatar-fill: var(--accent-purple-alt);
    --color-close-button-stroke: var(--background-level-1);
    --color-close-button-fill: var(--text-primary-alt);

    --text-color-chart-tooltip-primary: var(--text-inverted);
    --text-color-chart-tooltip-secondary: var(--text-secondary);
    --text-button-primary: var(--text-inverted);
    --text-button-secondary: var(--text-primary);
    --text-link-primary: var(--brand-primary);

    --background-overlay: rgba(0, 0, 0, 0.1);
    --background-skeleton-main: rgba(0, 0, 0, 0.1);
    --background-skeleton-glow: rgba(0, 0, 0, 0.15);
    --background-dot-active: var(--text-secondary);
    --background-input-disabled: var(--brand-secondary);

    --background-notification-success: var(--validation-success-alt);
    --background-notification-warning: var(--validation-warning-alt);
    --background-notification-error: var(--validation-alert-alt);
    --background-notification-info: var(--validation-info-alt);

    --border-color-button-active: var(--text-secondary);
    --border-color-button-outline: var(--border-level-2);
    --border-dot-outline: var(--background-dot-active);
    --border-tab-indicator: var(--text-primary);
    --border-account-selector: var(--border-level-1);

    --color-asset-type-EQ: #fa9b1e;
    --color-asset-type-BO: #526ae5;
    --color-asset-type-ETH: #368aff;
    --color-asset-type-ETF: #2bc597;
    --color-asset-type-MF: #b95eed;
    --color-asset-type-WA: #a54fc1;
    --color-asset-type-CO: #cc5600;
    --color-asset-type-ETC: #2bc597;
    --color-asset-type-FX: rgb(0, 120, 15); /** from stgo **/
    --color-asset-type-FU: rgb(80, 95, 130); /** from stgo **/
    --color-asset-type-CFD: rgb(162, 38, 52); /** from stgo **/
    --color-asset-type-INDEX: rgb(10, 95, 180); /** from stgo **/
  }
}

@layer utilities {
  /* Hide scrollbar for Chrome, Safari and Opera */
  .hide-scrollbar::-webkit-scrollbar {
    display: none;
  }
  /* Hide scrollbar for IE, Edge and Firefox */
  .hide-scrollbar {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .bg-product-icon-EQO {
    background: theme('colors.asset-type-EQ');
    background: linear-gradient(
      135deg,
      theme('colors.asset-type-EQ') 0%,
      theme('colors.asset-type-EQ') 50%,
      theme('colors.asset-type-CO') 50%,
      theme('colors.asset-type-CO') 100%
    );
  }

  .linear-gradient-secondary-to-top {
    background-image: linear-gradient(
      to top,
      var(--background-level-1),
      rgba(var(--background-level-1-raw), 0)
    );
  }

  .linear-gradient-secondary-to-bottom {
    background-image: linear-gradient(
      to bottom,
      var(--background-level-1),
      rgba(var(--background-level-1-raw), 0)
    );
  }

  @keyframes wave-lines {
    0% {
      background-position: -468px 0;
    }
    100% {
      background-position: 468px 0;
    }
  }
  .skeleton-gradient {
    background: var(--background-skeleton-main);
    background: linear-gradient(
      to right,
      var(--background-skeleton-main) 8%,
      var(--background-skeleton-glow) 18%,
      var(--background-skeleton-main) 33%
    );
    background-size: 800px 100px;
    animation: wave-lines 1500ms infinite ease-out;
  }
}

html,
body,
#root {
  height: 100%;
}

body {
  overflow: hidden;
  background-color: var(--background-global-canvas);
  user-select: none;
}

@font-face {
  font-family: 'Inter-Regular';
  src: local('Inter-Regular'), url('./fonts/Inter-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Inter-SemiBold';
  src: local('Inter-Semibold'), url('./fonts/Inter-SemiBold.ttf') format('truetype');
}

@font-face {
  font-family: 'Inter-Bold';
  src: local('Inter-Bold'), url('./fonts/Inter-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Inter-ExtraBold';
  src: local('Inter-ExtraBold'), url('./fonts/Inter-ExtraBold.ttf') format('truetype');
}

button,
[tabindex] {
  outline: none;
}

/** Calendar component */
.react-calendar__navigation {
  @apply flex;
}

.react-calendar__navigation__prev-button,
.react-calendar__navigation__next-button {
  @apply order-2;
  @apply px-2;
  @apply text-link-primary;
  @apply text-4xl;
}

.react-calendar__navigation__label {
  @apply text-left;
  @apply font-semibold;
}

/* day selection view */
.react-calendar__month-view__weekdays {
  @apply py-2;
  @apply text-secondary;
  @apply uppercase;
  @apply text-xs;
  @apply text-center;
}

.react-calendar__month-view__weekdays__weekday abbr {
  @apply no-underline;
}

.react-calendar__month-view__days__day {
  @apply py-2;
  @apply flex;
  @apply justify-center;
}

.react-calendar__month-view__days__day.react-calendar__tile--active {
  @apply py-0;
  @apply items-center;
}

.react-calendar__month-view__days__day.react-calendar__tile--active abbr {
  @apply inline-block;
  @apply rounded-full;
  @apply w-8;
  @apply h-8;
  @apply leading-8;
  @apply bg-brand-primary;
  @apply text-white;
}

/* month, year selection view */
.react-calendar__year-view,
.react-calendar__decade-view {
  @apply pt-3;
}

.react-calendar__year-view__months__month,
.react-calendar__decade-view__years__year {
  @apply py-4;
}

.react-calendar__year-view__months__month.react-calendar__tile--hasActive,
.react-calendar__decade-view__years__year.react-calendar__tile--hasActive {
  @apply text-link-primary;
}

.react-calendar__tile[disabled] {
  @apply text-secondary;
}
